import Vue from 'vue';
import Component from 'vue-class-component';

import { bottomNav, bottomNavItem } from 'muse-ui/bottomNav';
import { State, Action } from 'vuex-class';

@Component({
    components: {
        muBottomNav: bottomNav,
        muBottomNavItem: bottomNavItem
    }
})
export default class TabComp extends Vue {
    // 底部栏激活值
    @State(state => state.tabValue)
    tabValue: string;

    @Action('updateTabValue')
    updateTabValue;

    mounted() {
        this.updateTabValue(this['$route'].name);
        // this.tabValue = ;
    }

    // 改变值
    changeTab(val) {
        // this.tabValue = val;
        this.updateTabValue(val);
    }
}



